form {
    width: 400px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #EEE;
    margin: 20px auto;
    border-radius: 20px;
}

input {
    width: 200px;
    height: 30px;
    border-radius: 10px;
    font-size: 20px;
}

button {
    width: 100px;
    height: 35px;
    border-radius: 10px;
}

h1{
    text-align: center;
}

.github-profile{
    margin: 20px;
    width: 98%;
    background-color: #EEE;
}

img {
    width: 200px;
    height: 200px;
    border-radius: 30px;
}

img:hover{
    width: 400px;
    height: 400px;
}

.info {
    position: absolute;
    left: 500px;
    font-size: 30px;
    font-weight: 600;
}